@color: #f65c8f;
html {
    height: 100%;
    body {
        height: 100%;
        background-color: #f6f6f6;
    }
}

.title {
    padding: 0;
    text-align: center;
    background-color: @color;
    p {
        font-size: 17px;
        color: white;
        margin: 9px 0;
    }
}

.invitation {
    padding: 0;
    text-align: center;
    background-color: rgb(249,170,196);
    p {
        font-size: 12px;
        color: white;
        margin: 7px 0;
        span {
            color: @color;
        }
    }
}

.register-list {
    padding: 0;
    position: relative;
    margin-top: 18px;
    border-top: 1px solid #bcbcbc;
    .phone-num {
        width: 100%;
        border-left: medium none; 
        border-right: medium none; 
        border-top: medium none;
        border-bottom: 1px solid #bcbcbc;
        outline: white;
        padding: 12px 0px 12px 90px;
        font-size: 16px;
        color: #999;
        border-radius: 0;
        -moz-box-shadow: none;               /* For Firefox3.6+ */
        -webkit-box-shadow: none;            /* For Chrome5+, Safari5+ */
        box-shadow: none;  
    }

    .user-name {
        .phone-num;
    }

    .code {
        width: 100%;
        border-left: medium none; 
        border-right: medium none; 
        border-top: medium none;
        border-bottom: 1px solid #bcbcbc;
        outline:medium;
        padding: 12px 0px 12px 90px;
        font-size: 16px;
        color: #999;
        border-radius: 0;
    }

    .password {
        .code;
    }
    input::-webkit-input-placeholder { /* WebKit browsers */
        color: #999;
        font-size: 16px;
    }
    input:-moz-placeholder { /* Mozilla Firefox 4 to 18 */
        color: #999;
        font-size: 16px;
    }
    input::-moz-placeholder { /* Mozilla Firefox 19+ */
        color: #999;
        font-size: 16px;
    }
    input:-ms-input-placeholder { /* Internet Explorer 10+ */
        color: #999;
        font-size: 16px;
    }
    .left-blank {
        position: absolute;
        height: 88px;
        width: 12px;
        left: 0;
        top: 1px;
        z-index: 2;
        background-color: white;
    }
    .phone-img {
        padding-right: 23px;
        border-right: 1px solid #cacace;
        position: absolute;
        top: 7px;
        left: 12px;
        img {
            width: 15px;
        }
    }

    .key-img {
        padding-right: 23px;
        border-right: 1px solid #cacace;
        position: absolute;
        top: 52px;
        left: 12px;
        img {
            width: 15px;
        }
    }
    .get-code {
        position: absolute;
        right: 5px;
        bottom: 5px;
        border-radius: 8px;
        padding: 9px 16px;
        font-size: 14px;
        background-color: @color;
        color: white;
    }

}

.submit-btn {
    padding: 15px;
    button {
        width: 100%;
        padding: 13px;
        font-size: 17px;
        color: #ffffff;
        background-color: #a6a6ad;
        border-radius: 1000px;
    }
    .active {
        background-color: @color;
    }
}
.warning {
    position: fixed;
    z-index: 999;
    width: 110px;
    height: 100px;
    background-color: #484848;
    border-radius: 7px;
    top: 40%;
    margin-top: -50px;
    left: 50%;
    margin-left: -50px;
    padding: 10px;
    text-align: center;
    display: none;
    p {
        color: #E6E6E6;
        font-size: 14px;
        margin: 0;
        text-align: center;      
    }
}